// #region global config functions

import { PageHeaderConfig } from '@delon/abc';
export function fnPageHeaderConfig(): PageHeaderConfig {
  return {
    ...new PageHeaderConfig(),
    homeI18n: 'home',
  };
}

import { DelonAuthConfig } from '@delon/auth';
export function fnDelonAuthConfig(): DelonAuthConfig {
  return {
    ...new DelonAuthConfig(),
    login_url: '/passport/login',
  };
}

// tslint:disable-next-line: no-duplicate-imports
import { STConfig } from '@delon/abc';
export function fnSTConfig(): STConfig {
  return {
    ...new STConfig(),
    modal: { size: 'lg' },
  };
}

const GLOBAL_CONFIG_PROVIDES = [
  // TIPSÃ¯Â¼Â@delon/abc Ã¦ÂÂÃ¥Â¤Â§Ã©ÂÂÃ§ÂÂÃ¥ÂÂ¨Ã¥Â±ÂÃ©ÂÂÃ§Â½Â®Ã¤Â¿Â¡Ã¦ÂÂ¯Ã¯Â¼ÂÃ¤Â¾ÂÃ¥Â¦ÂÃ¨Â®Â¾Ã§Â½Â®Ã¦ÂÂÃ¦ÂÂ `st` Ã§ÂÂÃ©Â¡ÂµÃ§Â ÂÃ©Â»ÂÃ¨Â®Â¤Ã¤Â¸Âº `20` Ã¨Â¡Â
  { provide: STConfig, useFactory: fnSTConfig },
  { provide: PageHeaderConfig, useFactory: fnPageHeaderConfig },
  { provide: DelonAuthConfig, useFactory: fnDelonAuthConfig },
];

// #endregion

@NgModule({
  imports: [AlainThemeModule.forRoot(), DelonACLModule.forRoot(), ...MOCK_MODULES],
})
export class DelonModule {
  constructor(@Optional() @SkipSelf() parentModule: DelonModule) {
    throwIfAlreadyLoaded(parentModule, 'DelonModule');
  }

  static forRoot(): ModuleWithProviders {
    return {
      ngModule: DelonModule,
      providers: [...REUSETAB_PROVIDES, ...GLOBAL_CONFIG_PROVIDES],
    };
  }
}
